import React from 'react';
import './loginsass/login.scss';
var token = require("./token.js");
export default class LoGin extends React.Component{
	constructor(props){
		super(props)
		this.state={}
	}
	
	 search(){
        const inpVal = this.input.value;
        console.log(inpVal);
    }
	login(){
		 let inpVal = this.input.value;
       
		let crypto=token.createToken({
   	username:inpVal,
   
   },3600);
		
		console.log(crypto)
		console.log(window.localStorage.setItem)
		
		localStorage.setItem('login',crypto)
		localStorage.setItem('login',crypto)
			
		
	}
	
	render(){
		return <div className="login">
			<h1></h1>
			<div className="username">
				<input type="text" ref={input => this.input = input}  placeholder=""/>
				<button onClick={this.search.bind(this)}></button>
			</div>
			<div className="ma">
				<input type="text" placeholder=""/>
			</div>
			<p>
			
			<span className="yanse">{''}</span>
			
			<span className="yanse">{''}</span>
			</p>
			<div className="log">
				<input type="button" value="" onClick={this.login.bind(this)} />
			</div>
		</div>
	}
}
